import React, { useState, useCallback } from 'react';
import { Socket } from 'socket.io-client';

interface TmuxControlsProps {
  socket: Socket | null;
  sessionId: string;
  enabled?: boolean;
}

const TmuxControls: React.FC<TmuxControlsProps> = ({ socket, sessionId, enabled = true }) => {
  const [tmuxActive, setTmuxActive] = useState(false);
  const [isCheckingTmux, setIsCheckingTmux] = useState(false);

  // Send command to terminal
  const sendCommand = useCallback((command: string) => {
    console.log('=== TmuxControls Debug ===');
    console.log('Command to send:', command);
    console.log('Socket object:', socket);
    console.log('Socket connected?:', socket?.connected);
    console.log('SessionId:', sessionId);
    console.log('Enabled prop:', enabled);
    
    if (!socket || !sessionId) {
      console.error(' TmuxControls: Missing requirements');
      console.error('Socket:', socket);
      console.error('SessionId:', sessionId);
      alert('Terminal not connected. Please wait for terminal to initialize.');
      return;
    }

    if (!socket.connected) {
      console.error(' Socket exists but not connected');
      alert('Socket not connected. Please refresh the page.');
      return;
    }

    try {
      console.log(' Sending command via socket.emit...');
      const payload = {
        id: sessionId,
        data: command
      };
      console.log('Payload:', payload);
      socket.emit('terminal:data', payload);
      console.log(' Command sent successfully');
    } catch (error) {
      console.error(' TmuxControls: Error sending command:', error);
      alert('Error sending command: ' + error);
    }
  }, [socket, sessionId, enabled]);

  // Test echo command first
  const handleTestEcho = useCallback(() => {
    console.log(' TEST BUTTON CLICKED');
    console.log('Socket:', socket);
    console.log('Socket type:', typeof socket);
    console.log('Socket keys:', socket ? Object.keys(socket) : 'null');
    console.log('SessionId:', sessionId);
    console.log('Enabled:', enabled);
    
    // Try to see what the socket object actually is
    if (socket) {
      console.log('Socket.connected:', socket.connected);
      console.log('Socket.emit exists?:', typeof socket.emit);
    }
    
    if (!socket) {
      alert('Socket is null - not connected to backend');
      return;
    }
    if (!sessionId) {
      alert('SessionId is null - no terminal session');
      return;
    }
    
    console.log(' Calling sendCommand...');
    sendCommand('echo "Testing tmux button"\r');
  }, [sendCommand, socket, sessionId, enabled]);

  // Start tmux session
  const handleStartTmux = useCallback(() => {
    if (tmuxActive) {
      console.log('TmuxControls: tmux already active');
      return;
    }

    console.log(' START TMUX BUTTON CLICKED');
    console.log('Socket:', socket);
    console.log('SessionId:', sessionId);
    console.log('Socket connected:', socket?.connected);
    
    // First check if tmux is installed
    console.log(' Checking tmux availability...');
    sendCommand('which tmux\r');
    
    // Give it a moment then start tmux
    setTimeout(() => {
      console.log(' Starting tmux session...');
      sendCommand('tmux new-session\r');
      setTmuxActive(true);
    }, 500);
    
    // Reset state after 30 seconds (in case user exits tmux)
    setTimeout(() => {
      setTmuxActive(false);
    }, 30000);
  }, [tmuxActive, sendCommand, socket, sessionId]);

  // Split horizontal (Ctrl+B then %)
  const handleSplitHorizontal = useCallback(() => {
    if (!tmuxActive) {
      console.log('TmuxControls: Start tmux first');
      return;
    }
    console.log('TmuxControls: Splitting horizontal');
    sendCommand('\x02%'); // \x02 is Ctrl+B
  }, [tmuxActive, sendCommand]);

  // Split vertical (Ctrl+B then ")
  const handleSplitVertical = useCallback(() => {
    if (!tmuxActive) {
      console.log('TmuxControls: Start tmux first');
      return;
    }
    console.log('TmuxControls: Splitting vertical');
    sendCommand('\x02"');
  }, [tmuxActive, sendCommand]);

  // Navigate to next pane (Ctrl+B then arrow)
  const handleNextPane = useCallback(() => {
    if (!tmuxActive) {
      console.log('TmuxControls: Start tmux first');
      return;
    }
    console.log('TmuxControls: Switching to next pane');
    sendCommand('\x02o'); // Ctrl+B then o (next pane)
  }, [tmuxActive, sendCommand]);

  // Close current pane (Ctrl+B then x)
  const handleClosePane = useCallback(() => {
    if (!tmuxActive) {
      console.log('TmuxControls: Start tmux first');
      return;
    }
    console.log('TmuxControls: Closing pane');
    sendCommand('\x02x'); // Ctrl+B then x
    // Send 'y' to confirm
    setTimeout(() => sendCommand('y'), 100);
  }, [tmuxActive, sendCommand]);

  // Add mount debugging - must be before any conditional returns
  React.useEffect(() => {
    console.log(' TmuxControls mounted!');
    console.log('Initial socket:', socket);
    console.log('Initial sessionId:', sessionId);
    console.log('Initial enabled:', enabled);
    return () => {
      console.log(' TmuxControls unmounting');
    };
  }, [socket, sessionId, enabled]);

  // If not enabled, return null
  if (!enabled) {
    return null;
  }

  return (
    <div 
      className="tmux-controls"
      style={{
        display: 'inline-flex',
        alignItems: 'center',
        gap: '4px',
        marginLeft: '8px',
        padding: '0 8px',
        borderLeft: '1px solid rgba(255, 255, 255, 0.1)',
        height: '100%',
        zIndex: 10000,  // Make sure it's on top
        position: 'relative'
      }}
    >
      {!tmuxActive ? (
        <>
          <button
            className="terminal-control-btn"
            onClick={(e) => {
              e.preventDefault();
              e.stopPropagation();
              console.log(' Test button clicked directly!');
              handleTestEcho();
            }}
            title="Test if socket connection works"
            style={{
              padding: '4px 8px',
              fontSize: '12px',
              background: 'rgba(251, 191, 36, 0.1)',
              border: '1px solid rgba(251, 191, 36, 0.3)',
              color: '#fbbf24',
              borderRadius: '4px',
              cursor: 'pointer',
              marginRight: '4px',
              fontWeight: '500',
              pointerEvents: 'auto',
              zIndex: 10001
            }}
          >
             Test
          </button>
          <button
            className="terminal-control-btn"
            onClick={handleStartTmux}
          title="Start tmux for multiple terminal panes"
          style={{
            padding: '4px 8px',
            fontSize: '12px',
            background: 'rgba(34, 197, 94, 0.1)',
            border: '1px solid rgba(34, 197, 94, 0.3)',
            color: '#22c55e',
            borderRadius: '4px',
            cursor: 'pointer',
            transition: 'all 0.2s',
            fontWeight: '500'
          }}
          onMouseEnter={(e) => {
            e.currentTarget.style.background = 'rgba(34, 197, 94, 0.2)';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.background = 'rgba(34, 197, 94, 0.1)';
          }}
        >
           Start tmux
        </button>
        </>
      ) : (
        <>
          <span style={{
            fontSize: '11px',
            color: '#22c55e',
            fontWeight: '500',
            marginRight: '4px'
          }}>
             tmux
          </span>
          <button
            className="tmux-btn"
            onClick={handleSplitHorizontal}
            title="Split horizontal (Ctrl+B %)"
            style={{
              padding: '2px 6px',
              fontSize: '16px',
              background: 'transparent',
              border: '1px solid rgba(255, 255, 255, 0.2)',
              color: '#a9b1d6',
              borderRadius: '3px',
              cursor: 'pointer',
              transition: 'all 0.2s',
              lineHeight: '1'
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.background = 'rgba(255, 255, 255, 0.1)';
              e.currentTarget.style.color = '#ffffff';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.background = 'transparent';
              e.currentTarget.style.color = '#a9b1d6';
            }}
          >
            
          </button>
          <button
            className="tmux-btn"
            onClick={handleSplitVertical}
            title="Split vertical (Ctrl+B &quot;)"
            style={{
              padding: '2px 6px',
              fontSize: '16px',
              background: 'transparent',
              border: '1px solid rgba(255, 255, 255, 0.2)',
              color: '#a9b1d6',
              borderRadius: '3px',
              cursor: 'pointer',
              transition: 'all 0.2s',
              lineHeight: '1'
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.background = 'rgba(255, 255, 255, 0.1)';
              e.currentTarget.style.color = '#ffffff';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.background = 'transparent';
              e.currentTarget.style.color = '#a9b1d6';
            }}
          >
            
          </button>
          <button
            className="tmux-btn"
            onClick={handleNextPane}
            title="Next pane (Ctrl+B o)"
            style={{
              padding: '2px 6px',
              fontSize: '14px',
              background: 'transparent',
              border: '1px solid rgba(255, 255, 255, 0.2)',
              color: '#a9b1d6',
              borderRadius: '3px',
              cursor: 'pointer',
              transition: 'all 0.2s',
              lineHeight: '1'
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.background = 'rgba(255, 255, 255, 0.1)';
              e.currentTarget.style.color = '#ffffff';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.background = 'transparent';
              e.currentTarget.style.color = '#a9b1d6';
            }}
          >
            
          </button>
          <button
            className="tmux-btn"
            onClick={handleClosePane}
            title="Close pane (Ctrl+B x)"
            style={{
              padding: '2px 6px',
              fontSize: '14px',
              background: 'transparent',
              border: '1px solid rgba(239, 68, 68, 0.3)',
              color: '#ef4444',
              borderRadius: '3px',
              cursor: 'pointer',
              transition: 'all 0.2s',
              lineHeight: '1'
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.background = 'rgba(239, 68, 68, 0.1)';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.background = 'transparent';
            }}
          >
            
          </button>
        </>
      )}
    </div>
  );
};

export default TmuxControls;