/**
 * Session Summary Service
 * 
 * Handles session data collection and Claude Code communication
 * for generating comprehensive session summaries
 */

interface SessionData {
  openFiles: Array<{
    path: string;
    name: string;
    content: string;
    isDirty: boolean;
  }>;
  activeFile: string | null;
  terminalHistory: string;
  terminalCommands: string[];
  sessionDuration: number;
  projectStructure: string[];
  checkpoints: any[];
}

interface SessionSummaryResponse {
  success: boolean;
  summary?: string;
  error?: string;
}

export class SessionSummaryService {
  private static instance: SessionSummaryService;
  private baseURL: string;

  constructor() {
    // Use the main server API endpoint
    this.baseURL = window.location.origin;
  }

  public static getInstance(): SessionSummaryService {
    if (!SessionSummaryService.instance) {
      SessionSummaryService.instance = new SessionSummaryService();
    }
    return SessionSummaryService.instance;
  }

  /**
   * Collect current session data from the IDE
   */
  public collectSessionData(
    openFiles: any[],
    activeFile: string | null,
    terminalHistory: string,
    terminalCommands: string[]
  ): SessionData {
    const sessionStart = sessionStorage.getItem('session_start_time');
    const startTime = sessionStart ? parseInt(sessionStart) : Date.now();
    const sessionDuration = Math.floor((Date.now() - startTime) / 1000 / 60); // minutes

    return {
      openFiles: openFiles.map(file => ({
        path: file.path,
        name: file.name,
        content: file.content.substring(0, 2000), // Limit content length
        isDirty: file.isDirty
      })),
      activeFile,
      terminalHistory: terminalHistory.substring(-3000), // Last 3000 chars
      terminalCommands: terminalCommands.slice(-20), // Last 20 commands
      sessionDuration,
      projectStructure: [], // TODO: Could be enhanced to include file tree
      checkpoints: [] // TODO: Could include checkpoint data
    };
  }

  /**
   * Generate session summary using Claude Code
   */
  public async generateSessionSummary(sessionData: SessionData): Promise<SessionSummaryResponse> {
    try {
      const prompt = this.buildSessionSummaryPrompt(sessionData);
      
      const response = await fetch(`${this.baseURL}/api/claude/session-summary`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          sessionData,
          prompt
        })
      });

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }

      const result = await response.json();
      return {
        success: true,
        summary: result.summary
      };

    } catch (error) {
      console.error('Failed to generate session summary:', error);
      
      // Provide fallback summary if API fails
      const fallbackSummary = this.generateFallbackSummary(sessionData);
      
      return {
        success: false,
        summary: fallbackSummary,
        error: error instanceof Error ? error.message : 'Unknown error'
      };
    }
  }

  /**
   * Build the structured prompt for Claude Code
   */
  private buildSessionSummaryPrompt(sessionData: SessionData): string {
    const activeFiles = sessionData.openFiles.filter(f => f.content.trim().length > 0);
    const dirtyFiles = sessionData.openFiles.filter(f => f.isDirty);
    
    return `Please do an in-depth summarization of the session we just had. Explain:

**Session Context:**
- Session Duration: ${sessionData.sessionDuration} minutes
- Active Files (${activeFiles.length}): ${activeFiles.map(f => f.name).join(', ')}
- Modified Files (${dirtyFiles.length}): ${dirtyFiles.map(f => f.name).join(', ')}
- Current Focus: ${sessionData.activeFile || 'No active file'}

**File Contents:**
${activeFiles.map(file => `
### ${file.name} ${file.isDirty ? '(modified)' : ''}
\`\`\`
${file.content}
\`\`\`
`).join('\n')}

**Terminal Activity:**
Recent Commands: ${sessionData.terminalCommands.join(', ')}

Terminal Output:
\`\`\`
${sessionData.terminalHistory}
\`\`\`

**Analysis Required:**
- What we did (completed tasks, changes made)
- What went wrong (errors, issues encountered)  
- What went right (successful implementations, breakthroughs)
- What we're working on now (current focus, active development)
- What needs to be completed (remaining tasks, TODO items)
- How to do it moving forward (next steps, recommendations)

Please provide a comprehensive summary that will help the next agent understand the project state and continue development effectively. Format as a structured document with clear sections and actionable insights.`;
  }

  /**
   * Generate fallback summary when API is unavailable
   */
  private generateFallbackSummary(sessionData: SessionData): string {
    const activeFiles = sessionData.openFiles.filter(f => f.content.trim().length > 0);
    const dirtyFiles = sessionData.openFiles.filter(f => f.isDirty);
    
    return `# Session Summary (Generated Locally)

## Session Overview
- **Duration**: ${sessionData.sessionDuration} minutes
- **Files Worked On**: ${activeFiles.length} files
- **Modified Files**: ${dirtyFiles.length} files
- **Current Focus**: ${sessionData.activeFile || 'No active file'}

## Active Files
${activeFiles.map(file => `- **${file.name}** ${file.isDirty ? '(modified)' : ''}`).join('\n')}

## Recent Terminal Commands
${sessionData.terminalCommands.map(cmd => `- \`${cmd}\``).join('\n')}

## Modified Files Details
${dirtyFiles.map(file => `
### ${file.name}
\`\`\`
${file.content.substring(0, 500)}${file.content.length > 500 ? '...' : ''}
\`\`\`
`).join('\n')}

## Notes
- This summary was generated locally as Claude Code API was unavailable
- For detailed analysis, please run the session summary again when connected
- Consider saving your work and reviewing recent changes

## Next Steps
1. Review modified files for any unsaved changes
2. Test recent terminal commands and implementations
3. Consider creating a checkpoint before continuing
4. Document any issues or breakthroughs for future reference`;
  }
}

// Export singleton instance
export const sessionSummaryService = SessionSummaryService.getInstance();